@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600;700&display=swap);
:root{--root-font-size: 14px;--base-color: #FF4848;--secondary-color: #FFD371;--tag-color: #C2FFD9;--background-color: #9DDAC6}*{box-sizing:border-box}body,html,#root{font-size:14px;font-size:var(--root-font-size);height:100%;width:100%;padding:0;margin:0;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;font-family:"Open Sans",sans-serif}.no-button{border:0;margin:0;padding:0;outline:none;background:none}
.slider-wrapper{height:1rem;width:100%;position:relative}.slider-wrapper slide-bar{display:block;width:100%;height:100%}.slider-wrapper .current-value{position:absolute;bottom:100%;left:0;width:100%;height:100%}
.main-form .parent-form,.main-form .child-form{display:grid;grid-template-columns:repeat(2, minmax(0, 1fr));grid-template-rows:auto 3rem;align-content:start;align-items:center;grid-gap:1rem;padding:1rem}.main-form .parent-form .form-label,.main-form .parent-form .form-input,.main-form .child-form .form-label,.main-form .child-form .form-input{font-size:1rem}.main-form .parent-form label,.main-form .child-form label{text-transform:capitalize}
.main-container{flex-grow:0;height:-webkit-max-content;height:max-content;min-height:100%;display:flex;flex-flow:row nowrap}.main-container .dragger{flex-basis:.5rem;flex-grow:0;background-color:var(--tag-color);cursor:col-resize}.main-container .parent-box{flex-grow:1;display:flex;flex-flow:row wrap;justify-content:center;align-items:center;height:500px;background-color:var(--background-color)}.main-container .parent-box .child-box{display:flex;justify-content:center;align-items:center;font-size:1.5rem;color:#fff;flex:20% 1;min-height:20%;min-width:20%;padding:.5rem;background-image:linear-gradient(to right, var(--base-color), var(--base-color)),linear-gradient(to right, var(--secondary-color), var(--secondary-color));background-clip:content-box,padding-box}
.app{text-align:center;width:100%;height:100%;display:flex;flex-flow:row nowrap}.app .form-scroll{flex-grow:1;height:100%;overflow-y:auto}@media screen and (max-width: 500px){.app{flex-flow:column nowrap}.app .form-scroll{flex-basis:100%;flex-shrink:0}.app .main-container{align-self:flex-end;flex-basis:100%;flex-shrink:0}.app .main-container>div{height:100% !important}}
